import React, { Component } from 'react'
import { Wrapper } from './components/Styled';


export default class Hero extends Component {
  
  render() {
    return (
      <Wrapper>
        <p>
          Inteligencia de mercado,<br/>
          tecnologa y flexibilidad para<br/>
          afrontar los nuevos desafos
        </p>
      </Wrapper>
    )
  }
}






